		box-shadow: 0 0 0 0 rgba(199, 120, 15, 0.4), 0 0 0 4px rgba(199, 120, 15, 0.4), 0 0 0 15px rgba(199, 120, 15, 0), 0 0 0 20px rgba(199, 120, 15, 0);
	}
}

.btn-ripple {
	-webkit-animation: ripple 1.5s linear infinite;
	        animation: ripple 1.5s linear infinite;
	cursor: pointer;
	align-items: center;
	justify-content: center;
	border-radius: 9999px;
	--tw-bg-opacity: 1;
	background-color: rgba(255, 251, 245, var(--tw-bg-opacity));
	--tw-text-opacity: 1;
	color: rgba(199, 120, 15, var(--tw-text-opacity));
	transition-property: background-color, border-color, color, fill, stroke, opacity, box-shadow, -webkit-transform, -webkit-filter, -webkit-backdrop-filter;
	transition-property: background-color, border-color, color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
	transition-property: background-color, border-color, color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter, -webkit-transform, -webkit-filter, -webkit-backdrop-filter;
	transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
	transition-duration: 150ms;
	transition-duration: 700ms;
	transition-timing-function: linear;
}

.btn-ripple:hover {
	--tw-bg-opacity: 1;
	background-color: rgba(199, 120, 15, var(--tw-bg-opacity));
	--tw-text-opacity: 1;
	color: rgba(249, 236, 219, var(--tw-text-opacity));
}

.gallery-bg {
	background-size: cover;
	background-position: top;
	--tw-brightness: brightness(0.3);
	-webkit-filter: var(--tw-filter);
	        filter: var(--tw-filter);
	filter: var(--tw-filter);
}

.gallery-bg:hover {
	--tw-brightness: brightness(1.1);
	-webkit-filter: var(--tw-filter);
	        filter: var(--tw-filter);
}




